
.catalog-selection {
  margin-bottom: 25px;
}

.catalog {
  min-width: 280px;
  position: relative;
  padding: 0 20px 18px 20px;
  background-color: $basic_grey;
  margin-block: 24px;

  @media (min-width: $tablet-width) {
    height: 370px;
    max-width: 709px;
    margin: 0 auto;
    margin-bottom: 30px;
    padding-left: 52px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    height: 374px;
  }
}

@media (min-width: $desktop-width) {
  .catalog-selection__wrapper {
    display: flex;
    gap: 80px;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }
}

.catalog__title {
  position: relative;
  padding-top: 29px;
  margin-bottom: 30px;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
  padding-left: 71px;

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    max-width: 70%;
    margin-bottom: 10px;
    padding-top: 68px;
    padding-left: 0;
    font-size: 36px;
    line-height: 36px;
    text-transform: uppercase;
  }

  @media (min-width: $desktop-width) {
    padding-top: 76px;
    padding-bottom: 30px;
    padding-left: 168px;
    font-size: 36px;
    line-height: 36px;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 70px;
    height: 53px;
    top: 20px;
    left: 2px;

    @media (min-width: $tablet-width) {
      font-size: 36px;
      line-height: 36px;
    }

    @media (min-width: $tablet-width) and (max-width: $desktop-width) {
      top: 20px;
      right: 50px;
      width: 270px;
      height: 200px;
    }

    @media (min-width: $desktop-width) {
      top: 44px;
      left: 9px;
      width: 100px;
      height: 100px;
    }
  }
}

