.catalog__text {
  position: relative;
  padding-bottom: 21px;
  margin-bottom: 13px;
  border-bottom: 1px solid $catalog_line;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;

  @media (min-width: $tablet-width) {
    position: relative;
    max-width: 60%;
    display: inline-block;
    margin-bottom: 25px;
    border-bottom: 0;
    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    max-width: 95%;
    margin-bottom: 0;
  }
}

@media (min-width: $desktop-width) {
  .catalog__text br {
    display: none;
  }
}

@media (max-width: $mobile-width-only) {
  .catalog__text br {
    display: none;
  }
}

.catalog__link {
  display: block;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 16px;
  padding-block: 5px;

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
  }
}

.catalog__link svg {
  margin-left: 15px;
  margin-bottom: 2px;
}
