.main-intro {
  text-align: center;

  @media (min-width: $tablet-width) {
    grid-column-start: 1;
    grid-column-end: -1;
    // margin-inline: auto;
  }

  @media (min-width: $desktop-width) {
    max-height: 600px;
  }
}

.main-intro .main-intro--bg {
  position: relative;
  min-width: 280px;

  @media (min-width: $tablet-width) {
    background-image: none;
    margin-left: 0;
  }

  @media (min-width: $desktop-width) {
    padding-left: 73px;
    padding-top: 42px;
  }
}

.main-intro__picture {
  position: absolute;
  width: 100%;
  height: 302px;
  top: 0;
  background-image: linear-gradient($opacity_green, $opacity_green), url("../img/bg/cat-desktop.webp");
  background-color: $basic_green;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: local;

  @media (min-resolution: 2dppx) {
    background-image: linear-gradient($opacity_green, $opacity_green), url("../img/bg/cat-desktop@2x.webp");
  }

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    width: 50%;
    height: 812px;
    right: 0;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: -95px;
    left: 699px;
    transform: translate(0, -60px);
    display: block;
    min-width: 720px;
    height: 696px;
    background-size: contain;
  }
}

.main-intro__title {
  position: relative;
  color: $basic_white;
  padding-top: 27px;
  margin: 0;
  margin-bottom: 25px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;

  @media (min-width: $tablet-width) {
    padding-top: 21px;
    font-size: 60px;
    line-height: 60px;
  }
}

.main-intro__text {
  position: relative;
  color: $basic_white;
  margin-bottom: 27px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
  }
}

@media (min-width: $tablet-width) {
  .main-intro__title,
  .main-intro__text {
    display: block;
    margin-left: 97px;
    margin-bottom: 41px;
    min-width: 414px;
    text-align: left;
    color: $basic_color;
  }

  .main-intro__text {
    margin-bottom: 2px;
  }
}

@media (min-width: $desktop-width) {
  .main-intro__text {
    margin-bottom: 52px;
  }
}

.main-intro__picture-chiken-pro {
  position: relative;
  width: 300px;
  height: 275px;
  background-image: url("../img/chiken-pro.webp");
  background-size: 300px 270px;
  background-position: -20px 0;
  background-repeat: no-repeat;
  margin-inline: auto;

  @media (min-resolution: 1.5dppx) {
    background-image: url("../img/chiken-pro@2x.webp");
  }

  @media (min-width: $tablet-width) {
    display: block;
    width: 709px;
    height: 609px;
    background-position: 35px 20px;
    background-size: contain;
  }

  @media (min-width: $desktop-width) {
    max-width: 100%;
    height: 499px;
    top: -315px;
    left: 53px;
    background-size: 552px 499px;
    background-position: 65% 50%;
  }
}
